
import { defineComponent } from 'vue';
import { PAGE_SIZE } from '@/shared/constants/common.const';
import { ICM20100111_RES, boardNewsEventInquiryList } from '@/shared/type/ICM20100111.type';
import Pagination from '@/shared/components/Pagination/Pagination.vue'
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import SelectOption from "@/shared/components/SelectOption/SelectOption.vue";

export default defineComponent({
    name: 'ICM201001SC00',

    components: {
        Pagination,
        BreadCrumb,
        SelectOption
    },

    data() {
        const totalItems = 0 as number
        const currentPage = 1 as number
        const pageSize = PAGE_SIZE as number
        const startingIndex = 1 as number
        const isReset = false as boolean
        const newsEventList = [] as boardNewsEventInquiryList[];
        const searchForm = {
            searchValue: '',
            displayYN: '',
            statusCode: ''
        }
        const header = [
            { columnName: this.$t('icm201001sc00.column.no') },
            { columnName: this.$t('icm201001sc00.column.view') },
            { columnName: this.$t('icm201001sc00.column.title') },
            { columnName: this.$t('Status') },
            { columnName: this.$t('icm201001sc00.column.uploader') },
            { columnName: this.$t('icm201001sc00.column.created_date') },
            { columnName: this.$t('icm201001sc00.column.display') },
        ]

        return {
            newsEventList,
            searchForm,
            header,
            totalItems,
            currentPage,
            pageSize,
            startingIndex,
            isReset
        }
    },

    mounted() {
        this.requestNewEventList();
    },

    computed: {
        displayYN() {
            return this.$store.state.CommonCodeModule.commonCode.YnType
        },

        NewsEventCategoryCode() {
            return this.$store.state.CommonCodeModule.commonCode.NewsEventCategoryCode
        },
    },

    methods: {
        async requestNewEventList() {
            const reqBody = {
                pageSize: this.pageSize,
                displayYN: this.searchForm.displayYN,
                searchValue: this.searchForm.searchValue,
                pageNumber: this.currentPage,
                statusCode: this.searchForm.statusCode
            }
            const res = await this.$request.getRequest('ICM20100111', reqBody, false) as ICM20100111_RES;
            this.newsEventList = res?.body.list.map((item: boardNewsEventInquiryList) => {

                return {
                    title: item.title,
                    firstRegisterDate: this.$datetime.formatStringToStringDate(item.firstRegisterDate),
                    uploader: item.firstRegisterTellerName && item.firstRegisterTellerID ? `${item.firstRegisterTellerName} (${item.firstRegisterTellerID})` : "",
                    displayYN: this.$codeConverter.convertValue("YnType", item.displayYN),
                    newsEventID: item.newsEventID,
                    firstRegisterTellerName: item.firstRegisterTellerName,
                    firstRegisterTellerID: item.firstRegisterTellerID,
                    statusCode: `${item.statusCode} : ${this.$codeConverter.convertValue("ContentManangementStatusCode", item.statusCode)}`,
                }
            })
            this.totalItems = res.body.totalCount
        },

        handleClickAction(actionType: string) {
            switch (actionType) {
                case 'register':
                    this.$router.push({
                        path: `ICM201002SC01`,
                        query: { type: "register" }
                    });
                    break;
                case 'search':
                    if (this.currentPage > 1) {
                        this.currentPage = 1
                        this.isReset = true
                        this.requestNewEventList()
                    } else {
                        this.requestNewEventList()
                    }
                    break;
                default: //case reset
                    this.searchForm.searchValue = this.searchForm.displayYN = this.searchForm.statusCode = ''
                    this.currentPage = 1
                    this.isReset = true;
                    this.requestNewEventList()
                    break;
            }
        },

        handleRowClick(item) {
            this.$router.push({
                path: `ICM201002SC01`,
                query: {
                    type: "edit",
                    newsEventID: item.newsEventID
                }
            })
        },

        handlePagination(pageNumber: number) {
            this.isReset = false
            this.currentPage = pageNumber
            this.requestNewEventList()
        },

        handleStartingIndex(startingIndex: number) {
            this.startingIndex = startingIndex
        }
    }
})
